<template>
  <div>
      <div v-if="question.articleSource !== ''" class="post">
    <!-- <h1  @focusout="updateTitle">
      {{ question.articleTitle }}
    </h1> -->
    <a target="_blank" :href="question.articleURL">
      <blockquote class="tiktok-embed" @click="window.open(question.articleURL)" :cite="question.articleURL" :data-video-id="question.articleURL.match(/video\/(\d+)/)[1]" style="max-width: 605px;min-width: 325px;" >
      <section>
        <a target="_blank" :title="question.articleUser" href="https://www.tiktok.com/@aka87king?refer=embed">{{question.articleUser}}</a>
        <!-- <a title="" target="_blank" href="https://www.tiktok.com/tag/%E4%B8%AD%E7%A7%8B%E7%AF%80%E5%BF%AB%E6%A8%82?refer=embed">#</a> -->
        <!-- <a target="_blank" title="  -  -  " href="https://www.tiktok.com/music/--7117406208001051418?refer=embed">  -  -  </a> -->
      </section>
    </blockquote> 
    </a>

  </div>
  <div class="comment">
      <div v-for="item in question.comments" :key="item.id" class="comment-item">
        <el-avatar src="https://cube.elemecdn.com/3/7c/3ea6beec64369c2642b92c6726f1epng.png"></el-avatar>
        <p>{{item.content}}</p>
      </div>
      <line-base class="light" />

      <label style="font-weight:600;">Add Comment</label>
      <div class="type-in">
        <el-input v-model="comment" placeholder="write a comment..."></el-input>
        <button-base
        class="primary standard"
        :title="'Add'"
        @buttonPress="addComment"
        
      />

      </div>

    </div>
  </div>

</template>

<script src="vue-youtube/dist/vue-youtube.js"></script>
<script>
import Vue from 'vue'
import SurveyServices from '../../services/SurveyServices'
import store from '../../store/SurveyBuilder.js'
import VueYoutube from 'vue-youtube'
import ButtonBase from '../../components/ButtonBase.vue'
import LineBase from './LineBase.vue'

Vue.use(VueYoutube)

export default {
  name: 'PostTikTokEditable',
  store: store,
  data() {
    return {
      comment: ''
    }
  },
  props: {
    question: Object,
  },
  components: {
    ButtonBase,
    LineBase
},
  methods: {
    async addComment() {
      store.dispatch('addComment', {
        questionId: this.question.id,
        content: this.comment
      })
      this.question.comments.push({id:Math.random(),content: this.comment})
    },
    /**
     * Update and save title being changed
     */
    async updateTitle(e) {
      store.commit('updateValue', {
        value: e.target.innerHTML,
        parent: this.question,
        key: 'articleTitle',
      })

      await SurveyServices.patchQuestionType(this.question.id, {
        articleTitle: e.target.innerHTML,
      })
    },
  },
}
</script>

